import { SET_PIZZA_RECIPES, SET_SELECTED_PIZZA, ADD_TOPPING } from "../types/action.types"
import { InitialState, IPizza } from "../../models/store.model"
import { setPizzaRecipesAction } from '../actions/pizza.actions'
import { createReducer, createSlice } from '@reduxjs/toolkit'
export const initialState: InitialState = {
    customerInfo: null,
    pizzaRecipes: {
        pizzas: []
    },
    selectedPizza: null,
    router: null
}

const pizzaSlice = createSlice({
    name: 'pizzaRecipes',
    initialState: initialState,
    reducers: {
        addPizzaRecipes(state, action) {
            const { pizzaRecipes } = action.payload
            state.pizzaRecipes.pizzas.push(pizzaRecipes)
        },
        updateIngredientState(state, action) {
            const { pizzaKey, toppingKey } = action.payload
            let isAdded = state.pizzaRecipes.pizzas[pizzaKey].Toppings[toppingKey].isAdded
            isAdded = !isAdded // push({ isAdded: !state[pizzaKey].Toppings[toppingKey].isAdded })
        }
    }
})


// const actionsMap = {
//     [setPizzaRecipesAction.type]: (state: InitialState[]) => {
//         state
//     }
// }

export const { updateIngredientState, addPizzaRecipes } = pizzaSlice.actions

export default pizzaSlice.reducer

// const pizzaMakerReducerFn = createReducer(0, actionsMap)

export function pizzaMakerReducer(state = initialState, action) {
    switch(action.type) {

        case SET_PIZZA_RECIPES: {
            return {
                ...state,
                pizzas: action.pizzas
            }
        }

        case SET_SELECTED_PIZZA: {
            return {
                ...state,
                selectedPizza: state.pizzaRecipes.pizzas[action.key]
            }
        }

        case ADD_TOPPING: {
            
            return state

        }

        default: {
            return state
        }

    }
}